/* From: https://www.w3schools.com/css/tryit.asp?filename=trycss_tooltip_arrow_bottom */

.elementBottomStatusBar_tooltip__2zSbq {
  position: relative;
  display: inline-block;
  /* border-bottom: 1px dotted#222; */
}

.elementBottomStatusBar_tooltip__2zSbq .elementBottomStatusBar_tooltiptext__3_hz0 {
  opacity: 0;
  visibility: hidden;
  background-color: #222;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 150%;
  left: 50%;
  margin-left: -505px;
  width: 525px;
  -webkit-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
}

.elementBottomStatusBar_tooltip__2zSbq .elementBottomStatusBar_tooltiptext__3_hz0::after {
  content: "";
  position: absolute;
  top: 100%;
  right: 15px;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #222 transparent transparent transparent;
}

.elementBottomStatusBar_tooltip__2zSbq:hover .elementBottomStatusBar_tooltiptext__3_hz0 {
  opacity: 0.9;
  visibility: visible;
}
